.login-bg{
	position: fixed;
	width: 100%;
	min-height: 100%;
	background:url('../img/login-bg.jpg') no-repeat bottom center @portal-login-bg;
	background-size: cover;
	z-index: 1;
}
.login-main{
	position: absolute;
	left: 0;
	right: 0;
	top: 20%;
	z-index: 2;
}
.login-content{
	margin: auto;
	width: 1000px;
	.login-left{
		width:560px;
		float: left;
		color: #fff;
		padding-top: 60px;
		padding-left: 0;
		position: relative;
		z-index: 4;
		.login-left-tit{
			font-size: 55px;
			margin-bottom: @padding-large-vertical;
		}
		.login-left-cont{
			font-size: 16px;
		}
	}
}
.login-box{
	float: right;
	width: 380px;
	background: #000;
	background:  rgba(0,0,0,0.32);
	border-radius: @border-radius-base;
	position: relative;
	z-index: 6;
	.login-box-cont{
		display: none;
		padding: @padding-base-vertical*4 @padding-base-horizontal*3;
		.login-title{
			padding-bottom: @padding-base-horizontal*3;
			color: #fff;
			font-size: 24px;
		}
	}
	.login-box-tips{
		padding: @padding-large-vertical 0;
	}
	.login-box-control{
		margin-top: @padding-base-vertical;
		& + .login-box-control{
			margin-top: @padding-base-vertical;
		}
		color: #198CFF;
		.btn-lg{
			font-size: @font-size-large;
		}
		.link,
		a.link{
			color: #198CFF;
			&:hover{
				color:mix(#fff,#198CFF,20%);
			}
			&:active{
				color:mix(#000,#198CFF,10%);
			}
		}
	}
}
.login-form{
	.form-item{
		width: 100%;
		background-color: #fff;
		border-radius: @border-radius-base;
		margin-bottom: @padding-large-horizontal;
		&:last-child{
			margin-bottom: 0;
		}
	}
	.form-control{
		width: 100%;
		height: 40px;
		border: none;
		background: none;
		color: @text-weak-color;
		font-size: @font-size-base;
		&:focus{
			box-shadow: none;
		}
	}
	.input-group-addon{
		border: none;
		font-size: 16px;
		padding: 0 @padding-large-horizontal;
		color: @text-weaker-color;
	}
	.code-addon{
		&,img{
			width: 64px;
			height: 28px;
		}
	}
	&.login-form-group{
		border: 1px solid @border-color;
		border-radius: @border-radius-base;
		margin-bottom: @padding-large-horizontal;
		&:last-child{
			margin-bottom: 0;
		}
		.form-item{
			border-bottom: 1px solid @border-color;
			margin: 0;
			border-radius: @border-radius-base;
			&:first-child{
				border-bottom-left-radius: 0;
				border-bottom-right-radius: 0;
			}
			&:last-child{
				border-top-left-radius: 0;
				border-top-right-radius: 0;
				border-bottom: none;
			}
			.form-control{
				font-size: @font-size-large;
				padding-left: 0;
			}
		}
	}
}
