.pt-workspace-column,
.pt-workspace-row{
	.panel-normal{
		.panel-heading,
		.panel-body{
			padding: @padding-base-vertical*2 @padding-base-horizontal*3;
		}
		.panel-title{
			font-size: 16px;
		}
		.panel-heading + .panel-body{
			padding-top: 0;
		}
		.panel-body + .panel-heading{
			padding-top: 0;
		}
		.list-group{
			margin-bottom: 0;
		}
		.panel-normal{
			.panel-heading{
				padding: 0 @padding-large-horizontal;
			}
			.panel-title{
				font-size: @font-size-large;
			}
			.panel-body{
				padding: @padding-base-vertical @padding-large-horizontal;
			}
		}
		.btn-more{
			color: @text-weaker-color;
			background: none;
		}
	}
	.none-border-grid{
		border: none;
		.ui-jqgrid-view{
			.ui-jqgrid-hdiv,
			.ui-jqgrid-bdiv{
				border: none;
			}
			.ui-jqgrid-hdiv{
				border-bottom: 2px solid @divide-color;
			}
			.ui-jqgrid-htable{
				background: none;
				color: @text-weaker-color;
				.ui-th-column{
					border-right: none;
				}
			}
			.ui-jqgrid-btable{
				tr.jqgrow td{
					border-bottom: none;
					border-right: none;
				}
			}
		}
	}
	.ratio-progress-cell{
		position: relative;
		height: 8px;
		width: 100px;
		display: inline-block;
		vertical-align: middle;
		background: #f5f5f5;
		overflow: hidden;
		.progress-line{
			position: absolute;
			left: 0;
			top: 0;
			width: 0;
			height: 8px;
			background: #198CFF;
		}
	}
	.text-combobox{
		display: inline-block;
		vertical-align: middle;
		.form-control{
			padding-right: @padding-base-horizontal*3;
			border: none;
			.combobox-text{
				&,
				.combobox-placeholder{
					color: @brand-primary;
				}
			}
			.form-control-feedback{
				color: @brand-primary;
				font-family: 'FontAwesome';
				&:before{
					content: '\f107'
				}
			}
		}
	}
}

.pt-workspace-row{
	& + .pt-workspace-column,
	& + .pt-workspace-row{
		margin-top: @padding-base-vertical;
	}
}

.pt-workspace-column{
	border: 1px solid @border-color;
	background: @block-content-bg;
	&.column-one{
		.column-col{
			height: 310px;
			overflow: hidden;
		}
	}
	& + .pt-workspace-column,
	& + .pt-workspace-row{
		margin-top: @padding-base-vertical;
	}
	.panel-normal{
		border: none;
	}
	.column-col{
		position: relative;
		&:not(:first-child){
			&:before{
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				bottom: 0;
				width: 1px;
				background: @border-color;
			}
		}
	}
	.panel-chart-pie{
		margin: 0 auto @padding-large-vertical auto;
		width: 140px;
		height: 140px;
	}
	.panel-count-box{
		background: #0CB6E7;
		padding: @padding-base-vertical*2 @padding-base-horizontal*2;
		border-radius: @border-radius-base;
		text-align: center;
		color: #fff;
		margin-top: @padding-base-vertical*3;
		margin-bottom: @padding-base-vertical*7;
		.box-num{
			font-size: 24px;
			& + .box-cont{
				margin-top: @padding-base-vertical;
			}
		}
	}
	.panel-chart-info{
		.col{
			&:not(:first-child){
				&:before{
					content: '';
					position: absolute;
					top: 0;
					bottom: 0;
					left: 0;
					width: 1px;
					background: @divide-color;
				}
			}
		}
		.col-num,
		.col-cont{
			padding: 0 @padding-base-horizontal*3;
			white-space: nowrap;
			text-overflow: ellipsis;
			overflow: hidden;
		}
		.col-num{
			font-size: 24px;
			color: @text-color;
			& + .col-cont{
				margin-top: @padding-small-vertical;
			}
		}
		.col-cont{
			color: @text-weak-color;
			& + .col-num{
				margin-top: @padding-small-vertical;
			}
		}
	}
	.todo-list{
		.todo-cell{
			padding: @padding-base-vertical*2 @padding-base-horizontal*2;
			text-align: center;
			background: #F8F9FC;
			border-radius: @border-radius-base;
		}
		.todo-cell-tit{
			color: @text-color;
			font-size: 20px;
			& + .todo-cell-cont{
				margin-top: @padding-small-vertical;
			}
		}
		.todo-cell-cont{
			color: @text-weaker-color;
		}
	}
	.follow-list{
		.list-group-item{
			position: relative;
			border: none;
			color: @text-weak-color;
			padding-left: @padding-base-horizontal*2;
			&:before{
				content: '';
				width: 2px;
				height: 2px;
				background: @text-weak-color;
				position: absolute;
				left: @padding-base-horizontal;
				top: 50%;
				margin-top: -1px;
			}
			&:hover{
				color: @link-hover-color;
			}
			&:active{
				color: @link-active-color;
			}
		}
	}
	.quota-overview{
		.overview-col{
			&:not(:first-child){
				&:before{
					content: '';
					position: absolute;
					top: 0;
					bottom: 0;
					left: 0;
					width: 1px;
					background: @divide-color;
				}
			}
		}
		.overview-cell{
			padding-left: @padding-base-horizontal;
			padding-right: @padding-base-horizontal;
			.cell-tit,
			.cell-num,
			.cell-cont{
				font-size: @font-size-base;
				padding: 0 @padding-large-horizontal;
				white-space: nowrap;
				text-overflow: ellipsis;
				overflow: hidden;
			}
			.cell-tit{
				color: @text-weak-color;
			}
			.cell-num{
				font-size: 28px;
				color: @text-color;
			}
			.cell-cont{
				color: @text-weaker-color;
			}
			.cell-chart-box{
				height: 240px;
			}
		}
	}
	.subscribe-row{
		.subscribe-col{
			&:not(:first-child){
				&:before{
					content: '';
					position: absolute;
					top: 0;
					bottom: 0;
					left: 0;
					width: 1px;
					background: @divide-color;
				}
			}
		}
		.subscribe-box{
			.subscribe-num,
			.subscribe-tit,
			.subscribe-cont{
				white-space: nowrap;
				text-overflow: ellipsis;
				overflow: hidden;
				text-align: center;
			}
			.subscribe-num{
				font-size: 16px;
				color: @text-color;
			}
			.subscribe-tit{
				color: @text-weaker-color;
			}
			.subscribe-cont{
				color: @text-color;
			}	
			.subscribe-chart{
				margin: @padding-base-vertical 0;
				height: 80px;
			}
		}
	}
}

.pt-notice-list{
	.item{
		padding: @padding-base-vertical 0;
		border-bottom: 1px solid @divide-color;
		&:first-child{
			padding-top: 0;
		}
		&:last-child{
			padding-bottom: 0;
			border-bottom: 0;
		}
	}
	.item-head{
		.clearfix;
		& + .item-body{
			margin-top: @padding-base-vertical;
		}
	}
	.item-title,
	a.item-title{
		font-size: @font-size-h3;
		color: @text-color;
	}
	a.item-title{
		&:hover{
			color: @brand-primary;
		}
	}
}